@use '../../sass/tokens.sass' as *
@use '../../sass/z-index.sass' as *


.main-menu
  position: sticky
  top: 0
  z-index: $z-main-menu
  display: block

  &__list
    display: flex
    padding: 0
    gap: 20px

  &__item
    text-align: center
    list-style: none
    cursor: pointer

    &.current-page a
      color: $c-main
      text-decoration: underline
      text-decoration-color: rgba($c-main, .3)
      text-underline-offset: 3px
      text-decoration-thickness: 1px

  &__item > a
    display: flex
    height: 100%
    color: $c-black
    align-items: center

    &:hover
      color: $c-main

  &__sub-menu
    position: absolute
    top: 0
    left: -5px
    z-index: $z-sub-menu
    display: none
    padding: 30px 5px 5px
    background-color: $c-bg

    &.shown
      display: block

  &__sub-item
    text-align: left
    margin-bottom: 10px
    list-style: none
