@use './sass/reset'
@use './sass/fontface'
@use './sass/common'

@use './sass/imports' as *

#icon-composition
  fill: white

body.index-page
  display: grid
  font-family: sans-serif
  grid-template-columns: minmax(10px, 1fr) minmax(0, 1080px) minmax(10px, 1fr)

  & > *
    grid-column: 2/3

  & > .page-header,
  & > .hero,
  & > .page-main,
  & > .offers,
  & > .socials
    margin-bottom: 50px
