@use '../../sass/tokens.sass' as *

.offer
  display: block

  &__icon
    width: 32px
    height: 32px
    border-radius: 6px
    fill: none
    stroke: $c-main
    stroke-width: 8px
    stroke-linecap: round
    margin-bottom: 20px

    & svg
      width: 32px
      height: 32px

  &__title
    font-size: 20px
    font-weight: 700
    color: #000
    margin-bottom: 16px

  &__text
    color: #000

    &::first-letter
      text-transform: uppercase

