*, *::before, *::after
  box-sizing: inherit
  margin-top: 0
  transition: .3s color ease, .3s border ease

html
  box-sizing: border-box

html, body
  width: 100%
  margin: 0
  padding: 0
  min-height: 100%

svg
  max-width: 100%
  max-height: 100%

img
  display: block
  font-size: 24px
  color: red
  text-align: center
  max-width: 100%
  justify-content: center
  align-items: center

  &::before
    display: block
    height: 20px
    content: ''

  &::after
    font-size: 16px
    color: #000
    content: " (Image - Right click to reload if not loaded)"

a
  cursor: pointer
  text-decoration: none

button
  border: 0
  cursor: pointer
