@use './tokens.sass' as *

.visually-hidden
  position: absolute
  width: 1px
  height: 1px
  overflow: hidden
  white-space: nowrap
  clip-path: inset(50%)

.scroll-hidden
  -ms-overflow-style: none /* Для IE и Edge */
  scrollbar-width: none

  &::-webkit-scrollbar
    display: none !important
    background-color: red

.row-2
  display: flex
  gap: 13px
  flex-wrap: wrap

  & > *
    flex: 1 1 0

.page-main
  & li
    margin-bottom: 10px

a
  color: $c-main
